                    this.$message({
                        message: '请输入应用范围',
                        type: 'warning'
                    });
                    return false
                }
                // if (this.form.imgAddress.length < 6){
                //     this.$message({
                //         message: '请上传图片附件',
                //         type: 'warning'
                //     });
                //     return false
                // }

                if (this.form.linkMan== ''){
                    this.$message({
                        message: '请输入联系人',
                        type: 'warning'
                    });
                    return false
                }
                if (this.form.phoneNumber== ''){
                    this.$message({
                        message: '请输入联系人号码',
                        type: 'warning'
                    });
                    return false
                }
                if ( !Number(this.form.phoneNumber)  ){
                    this.$message({
                        message: '手机号码格式输入错误',
                        type: 'warning'
                    });
                    return false
                }
                this.$axios.post('/occupancy/addOccupancy',this.$qs.stringify(this.form))
                    .then((res)=>{
                    if (res.data.code==0){
                        this.$message({
                            message: '发布成功',
                            type: 'success'
                        });
                        that.$router.push('/sHome/Tachieve')
                    }else {
                        this.$message.error(res.data.msg);
                    }
                })
                    .catch(err=>{
                        this.$message.error('网络连接错误请稍后再试');
                    })

            },
            handleAvatarSuccess(res, file) {
                console.log(res);
                console.log(file);
            },
            handleRemove(file, fileList) {
                this.fileList = fileList;
                console.log(this.fileList)
            },
            handlePreview(file) {
                console.log(file);
            },
            handError(err, file, fileList){
                this.$message.error('文件超过500kb，上传失败')
            }
        },

    }
</script>

<style  lang="less">
  .el-form-item__content {
    margin-left: 180px!important;
  }
  .el-form-item__label {
    width: 165px!important;
  }
  .needForm1 {
    width: 753px;
    margin: 0 auto;
    .title {
      font-size: 18px;
      color: #333333;
      margin-top: 76px;
      margin-bottom: 56px;
      font-weight: 600;
      text-align: center;
    }
  }
  .needWarp {
    text-align: center;
    min-height: 561px;
    vertical-align: top;
    .warpL {
      width: 150px;
      height: 100%;
      display: inline-block;
      background-color: green;
      ul {
        li {
          list-style: none;
        }
      }
    }
    .warpR {
      width: 490px;
      height: 100%;
      display: inline-block;
      background-color: blue;
      vertical-align: top;
      ul {
        li {
          text-align: left;
          list-style: none;
        }
      }
    }
  }
  .upload {
    margin-left: 40px;
  }
  .applicant {
    width: 100%;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
  }
</style>



